export type FileStatus = "PENDING" | "PROCESSING" | "FAILED" | "DELETED" | "COMPLETED"
export type FileId = string
export type FileStatusState = typeof statusValues[number]
export type FileProgress = number
export type FileSize = number

export type FileCollection = {
  results: Array<ServerFile>
}

export type IndexStatus = {
  status: "PENDING" | "INDEXED" | "ANALYZING" | "INDEXING_FAILED" | "ANALYZE_FAILED"
}

export const statusValues = [
  "completed",
  "pending",
  "progress",
  "analyzing",
  "processing",
  "done",
  "error",
  "failed",
] as const

export const isValidStatus = (status: string): status is FileStatusState => {
  return statusValues.includes(status as FileStatusState)
}

export type DownloadFormat =
  | null
  | "analytics-csv"
  | "analytics-json"
  | "annotated"
  | "text-extraction"

export type FileExtension =
  | "pdf"
  | "doc"
  | "csv"
  | "xls"
  | "ppt"
  | "html"
  | "txt"
  | "xml"
  | "eml"
  | string

export type FileRowType = {
  id: FileId
  name: string
  type: FileExtension
  status: FileStatusState
  size: number
  date: string
  progress?: FileProgress
  sharedWith: Array<string> | null
  error?: string | null
  owner: string
}

export type ServerFile = {
  error: string | null
  file_id: string
  file_name: string
  folder_id: null
  raw_size: number
  status: FileStatus
  tags: null
  trashed: false
  upload_ts: string
  shared_with: Array<string> | null
  owner: string
}

export type NativeFile = {
  file: Buffer
  name: string
  type?: string
  size: number
  uri: string
}

export type RawFile = File | NativeFile

export type Uploadable = {
  file: RawFile
  src: string
  progress: number
  status: string
  fileId: string
  error?: string | null
  done?: boolean
  sharedWith: Array<string> | null
  owner: string
}

export type UploadOptions = {
  onFileId?: (src: string, fileId: string) => void
  location: "filesPage" | "mainChatModal" | "activeChatModal"
}

export type UploadingFilesTypes = {
  files: Array<Uploadable>
  uploading: Array<Uploadable>
  uploadError: Array<Uploadable>
  removeCompleted: (src: string) => void
  updateToDone: (id: string) => void
  doneIds: Record<string, boolean>
  addToUploadQueue: (files: Array<File | NativeFile>, options: UploadOptions) => Array<Uploadable>
}

export type ListSorterDirection = "asc" | "desc"

export type OnListSorterDirectionChange<T> = {
  sortBy: keyof T
  direction: ListSorterDirection
}

export type SeparatedFiles = {
  completed: Array<FileRowType>
  loading: Array<FileRowType>
}

export type PaginatedParams = {
  sortBy?: string
  direction?: string
  shared?: boolean
  fileName?: string
  ownership?: FilesInfiniteOwnership
  owners?: Array<string>
}

export type FilesCustomerIO = {
  id: string
  ownerIsUser: boolean
  ownerId: string
}

export type FilesInfiniteOwnership = "shared" | "owned" | "all"
